      for (let i = 0; i < 10; i++) {
        this.searchResults.push(this.searchResults.length + 1);
      }
    }
    , methods: {
      formatterTimePicker(type, val) {
        if (type === 'month') {
          return `${val}月`;
        } else if (type === 'year') {
          return `${val}年`;
        } else if (type === 'day') {
          return `${val}日`;
        }
        return val;
      }
      , dateDidSelect() {
        this.timeSelectTool.showTimePicker = false;
        console.log(this.timeSelectTool.currentDate)
